import axios from "axios";
import { createContext, useEffect, useState } from "react";

export const WishlistContext = createContext();

export function WishlistContextProvider(props) {
  const [numOfWishlistItems, setNumOfWishlistItems] = useState(0);

  function addProductToWishlist(id) {
    return axios
      .post(
        "https://ecommerce.routemisr.com/api/v1/wishlist",
        { productId: id },
        { headers: { token: localStorage.getItem("userToken") } }
      )
      .then((response) => response)
      .catch((error) => error);
  }


  function getLoggedWishlist() {
    return axios
      .get("https://ecommerce.routemisr.com/api/v1/wishlist", {
        headers: { token: localStorage.getItem("userToken") },
      })
      .then((response) => response)
      .catch((error) => error);
  }

  function removeProductWishlist(id) {
    return axios
      .delete(`https://ecommerce.routemisr.com/api/v1/wishlist/${id}`, {
        headers: { token: localStorage.getItem("userToken") },
      })
      .then((response) => response)
      .catch((error) => error);
  }




  async function wishlistCounter() {
    const { data } = await getLoggedWishlist();
    setNumOfWishlistItems(data?.count);
  }

  useEffect(() => {
    wishlistCounter();
  }, []);

  return (
    <WishlistContext.Provider
      value={{
        addProductToWishlist,
        getLoggedWishlist,
        removeProductWishlist,
        numOfWishlistItems,
        setNumOfWishlistItems,
      }}
    >
      {props.children}
    </WishlistContext.Provider>
  );
}
